import React, { Component } from 'react'
import './App.css';
import MainContainer from './mainContainer';
import Footer from './footer';
import "./home.css";

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      dataTweet: []
    };
  }

  render() {
    return (
      <div id="main-wrapper" className="main-wrapper">
          <div><MainContainer/></div>
          <div><Footer/></div>
      </div>
    )
  }
}

export default Home;
