import React from "react";
import SimpleSlider from './slick';

class SliderHome extends React.Component {
    state = { width: Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0), height: Math.max(document.documentElement.clientHeight || 0, window.innerHeight || 0) };
    updateDimensions = () => {
      this.setState({ width: window.innerWidth, height: window.innerHeight });
    };
    componentDidMount() {
      window.addEventListener('resize', this.updateDimensions);
    }
    componentWillUnmount() {
      window.removeEventListener('resize', this.updateDimensions);
    }
    render() {
        return <SimpleSlider resultSearchNasaAPOD={this.props.resultSearchNasaAPOD} slidesToShow={ Math.max(this.state.width || 0) <= 750 ? (Math.max(this.state.width || 0) <= 500 ? 1 : 2) : 3}/>;
      }
  }

  export default SliderHome;