import React, { Component } from "react";
import Slider from "react-slick";
import { Link, Redirect } from 'react-router-dom';
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";

export default class SimpleSlider extends Component {

    render() {
      const settings = {
        dots: true,
        infinite: true,
        arrows: false,
        adaptiveHeight: true,
        speed: 500,
        slidesToShow: this.props.slidesToShow || 3,
        slidesToScroll: 1,
        appendDots: dots => <ul>{dots}</ul>,
        customPaging: i => (
          <button>{i}</button>
        )
      };
      return (
        <div>
          <Slider {...settings}>
          <div className="col-sm" id="resume" >
            <Link to={{pathname: "/home/resume-work"}}>
              <img className="img-fluid" src={`../img/1.jpg`}/>
              <div className="bottom-centered left middle">RESUME - WORK</div>
            </Link>
          </div>
          <div className="col-sm" id="work">
            <Link to={{pathname: "/home/hobby"}}>
              <img className="img-fluid" src={`../img/4.jpg`}/>
              <div className="bottom-centered centered middle">HOBBY</div>
            </Link>
          </div>
          <div className="col-sm" id="about-me">
            <Link to={{pathname: "/home/about-me"}}>
              <img className="img-fluid" src={`../img/6.jpg`}/>
              <div className="bottom-centered right middle">ABOUT ME</div>
            </Link>
          </div>
          </Slider>
        </div>
      );
    }
  }