import React from "react";
import ReactPlayer from "react-player";
import ChartNearObject from "./nearObjects"
import Header from './headers'
import Footer from './footer';
import Lightroom from './lightboxGallery';
import "./hobby.css";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
const axios = require("axios").default;

class Hobby extends React.Component {
  constructor(props) {
    super(props);
    this.fileNames = {
    	json: "collection.json",
      csv: "collection.csv",
      text: "collection.txt"
    }   
    this.state = {
      handleValue: "",
      fileType: "json",
      fileDownloadUrl: null,
      selectedOptionNasa: "",
      startDate: new Date(),
      lastDate: new Date(),
      resultSearchNasaAPOD: [],
      resultSearchNasaNEO: {
        element_count : 0
      },
      resultSearchNasaCollection: {},
      dataTranslate: []
    };
    this.convertDate = this.convertDate.bind(this);
    this.getDateRange = this.getDateRange.bind(this);
    this.translateText = this.translateText.bind(this);
    this.download = this.download.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleHandleChange = this.handleHandleChange.bind(this);
    this.handleOptionChangeNasa = this.handleOptionChangeNasa.bind(this);
    this.handleStartDateChange = this.handleStartDateChange.bind(this);
    this.handleLastDateChange = this.handleLastDateChange.bind(this);
    this.searchQueryNasa = this.searchQueryNasa.bind(this);
    this.clickOptionChangeNasa = this.clickOptionChangeNasa.bind(this);
  }


  searchQueryNasa = (searchNasa) => {
    axios
    .get("/nasa/search/" + (searchNasa.handle ? searchNasa.handle : this.state.handleValue), {})
    .then(response => {
      if(!response.data.err){
        if (response.data) {
          this.setState({
            resultSearchNasaCollection: response.data.data,
          });
        }
      }
    })
    .catch(error => {
      console.log("login error", error);
    });
  }
  clickOptionChangeNasa(event) {
    if(this.state.selectedOptionNasa === "searchNasa"){
      let search = {"handle":this.state.handleValue};
      this.searchQueryNasa(search);
    }else{
      if(this.state.selectedOptionNasa === "NEO" && this.getDateRange(this.state.startDate, this.state.lastDate) >= 9){
        document.getElementById("errorContainer").className = "errorContainerLog";
        document.getElementById("errorContainer").innerText = "Veuillez respecter une marge de 7 jours maximum pour les NEO";
        document.getElementById("errorContainer").scrollIntoView();
        return;
      }
      var searchOptions = {
        find: this.state.selectedOptionNasa,
        startDate: this.convertDate(true,this.state.startDate).replace(/b/g, "-"),
        lastDate: this.convertDate(true,this.state.lastDate).replace(/b/g, "-"),
      }
      axios
      .get("/nasa/"+searchOptions.find+"/"+searchOptions.startDate+"/"+searchOptions.lastDate, {})
      .then(response => {
        if(!response.data.err){
          if (response.data) {
            if(searchOptions.find === "APOD"){
              this.setState({
                resultSearchNasaAPOD: response.data.data,
              });
            }else{
              this.setState({
                resultSearchNasaNEO: response.data.data,
              });
            }
          }
        }
      })
      .catch(error => {
        console.log("login error", error);
      });
    }
    this.handleChange();
  }
  handleHandleChange(event) {
    this.setState({ handleValue: event.target.value });
  }
  handleOptionChangeNasa(event) {
    this.setState({
      selectedOptionNasa: event.target.value,
    });
    this.handleChange();
  }
  handleStartDateChange(event) {
    this.setState({
      startDate: event,
    });
    this.handleChange();
  }
  handleLastDateChange(event) {
    this.setState({
      lastDate: event,
    });
  }
  handleChange() {
    this.setState({resultSearchNasaNEO: {element_count : 0}});
    this.setState({resultSearchNasaCollection: {}});
    document.getElementById("errorContainer").className = "";
    document.getElementById("errorContainer").innerHTML = "";
  }
  convertDate(en,inputFormat) {
    if(!en){
      function pad(s) { return (s < 10) ? '0' + s : s; }
      var d = new Date(inputFormat)
      return [d.getFullYear(), pad(d.getMonth()+1), pad(d.getDate())].join('b')
    }else{
      function pad(s) { return (s < 10) ? '0' + s : s; }
      var d = new Date(inputFormat)
      return [d.getFullYear(), pad(d.getMonth()+1), pad(d.getDate())].join('b')
    }
  }
  getDateRange(startDate, lastDate) {
    const date = new Date(startDate.getTime());
  
    const dates = [];
  
    while (date <= lastDate) {
      dates.push(new Date(date));
      date.setDate(date.getDate() + 1);
    }
  
    return dates.length;
  }
  translateText(resDesc) {
      var formData = new FormData();
      formData.append("q", resDesc);
      formData.append("source", "en");
      formData.append("target", "fr");
      formData.append("format", "text");
      formData.append("api_key", "");
      try {
          axios
          .post('https://libretranslate.de/translate', formData,{headers: {'Accept': 'application/json'}})
          .then(res => {
            console.log(res);
            this.state.dataTranslate.push(res.data.translatedText);
          }).catch((error) => {
              console.log(error);
          });
      } catch (error) {
        console.log(error);
      }
  }
  download (event) {
    event.preventDefault();
  	// Prepare the file
    let output;
    if (this.state.fileType === "json") {
    	output = JSON.stringify({nasa: this.state.resultSearchNasaCollection}, 
      	null, 4);
    } else if (this.state.fileType === "csv"){
      // Prepare data:
      let contents = [];
      contents.push (["State", "Electors"]);
      this.state.data.forEach(row => {
      	contents.push([row.state, row.electors])
      });
      output = this.makeCSV(contents);
    } else if (this.state.fileType === "text"){
      // Prepare data:
      output = '';
      this.state.data.forEach(row => {
      	output += `${row.state}: ${row.electors}\n`
      });
    }
    // Download it
    const blob = new Blob([output]);
    const fileDownloadUrl = URL.createObjectURL(blob);
    this.setState ({fileDownloadUrl: fileDownloadUrl}, 
      () => {
        this.dofileDownload.click(); 
        URL.revokeObjectURL(fileDownloadUrl);  // free up storage--no longer needed.
        this.setState({fileDownloadUrl: ""})
    })    
  }
  componentDidMount() {
    console.log("hobby");
  }
  // componentDidUpdate(prevProps, prevState){
  //   if (prevProps.resultSearchNasaAPOD !== this.props.resultSearchNasaAPOD) {
  //       for(var i=0; i<this.props.resultSearchNasaAPOD.length; i++){
  //        this.translateText(this.props.resultSearchNasaAPOD[i].explanation);
  //       }
  //       // this.setState({ dataTranslate: descriptionTranslate});
  //   }
  // }
  render() {
    const {dataTranslate, startDate, lastDate} = this.state;
    if(this.state.resultSearchNasaNEO.element_count === 0) {
      window.addEventListener("click", (event) => {
        var element = document.getElementById("hobbyContainer");
        if(element){
          if(document.getElementsByClassName("styles_lightroom__1X2qE") ? (document.getElementsByClassName("styles_lightroom__1X2qE")[0] ? document.getElementsByClassName("styles_lightroom__1X2qE")[0].style.visibility === "visible" : false) : false){
            element.style.height = "100vh";
            element.style["overflow-y"] = "hidden";
          }else{
            element.style.height = "";
            element.style["overflow-y"] = "";
          }
        }
      });
    }
    return (
      <div id="hobbyContainer" className="main-container-pos col-sm-12">
        <div id="header"><Header/></div>
          <div id="search-choix">
            <div className="radio-find">
              <div>Find :</div>
              <div className="div-radio">
                <input type="radio" id="apod" name="nasa" value="APOD" checked={this.state.selectedOptionNasa === "APOD"} onChange={this.handleOptionChangeNasa}/>
                <label htmlFor="apod">APOD</label>
              </div>
              <div className="div-radio">
                <input type="radio" id="neo" name="nasa" value="NEO" checked={this.state.selectedOptionNasa === "NEO"} onChange={this.handleOptionChangeNasa}/>
                <label htmlFor="neo">NEO</label>
              </div>
              <div className="div-radio">
                <input type="radio" id="searchNasa" name="nasa" value="searchNasa" checked={this.state.selectedOptionNasa === "searchNasa"} onChange={this.handleOptionChangeNasa}/>
                <label htmlFor="user">Search</label>
              </div>
              <div><button type="button" className="btn btn-outline-primary btn-sm" onClick={this.clickOptionChangeNasa}>Go</button></div>
              </div>
            </div>
            {(this.state.selectedOptionNasa === "APOD" || this.state.selectedOptionNasa === "NEO") &&
            <div id="dateSearchContainer">
              <div id="dateSearch">
              <DatePicker
                selected={startDate} 
                onChange={this.handleStartDateChange}
                dateFormat={"dd/MM/yyyy"}
                className={"date-search first-date-search"}
              />
              <DatePicker
                selected={lastDate} 
                onChange={this.handleLastDateChange}
                dateFormat={"dd/MM/yyyy"}
                className={"date-search"}
              />
              </div>
            </div>
            }
          <div className="" id="errorContainer"></div>
          <div id="search-crit">
            {(this.state.selectedOptionNasa === "searchNasa") &&
              <div className="col-sm-6 searchBar">
                <input 
                    id="searchQueryInput" 
                    type="text" 
                    value={this.state.handleValue}
                    onChange={this.handleHandleChange}
                    placeholder={`Write what you are looking for...` }
                  />
                <button id="searchQuerySubmit" type="submit" name="searchQuerySubmit" onClick={this.searchQueryNasa}>
                  <svg id="svgSearch" viewBox="0 0 24 24"><path fill="#666666" d="M9.5,3A6.5,6.5 0 0,1 16,9.5C16,11.11 15.41,12.59 14.44,13.73L14.71,14H15.5L20.5,19L19,20.5L14,15.5V14.71L13.73,14.44C12.59,15.41 11.11,16 9.5,16A6.5,6.5 0 0,1 3,9.5A6.5,6.5 0 0,1 9.5,3M9.5,5C7,5 5,7 5,9.5C5,12 7,14 9.5,14C12,14 14,12 14,9.5C14,7 12,5 9.5,5Z" />
                  </svg>
                </button>
              </div>
            }
          </div>

          <div className="container-hobby">
            <div id="hobbyMainContainer">
                {(this.state.resultSearchNasaNEO.element_count === 0 && Object.keys(this.state.resultSearchNasaCollection).length === 0) && <Lightroom resultSearchNasaAPOD={this.state.resultSearchNasaAPOD}/>}
                {Object.keys(this.state.resultSearchNasaCollection).length !== 0 &&
                    <div id="downloadCollection">
                      {/* <div id="resultSearchNasaCollection" className="result-container-APOD" key={index} data-key={index}></div> */}
                      <button className="btn btn-primary" onClick={this.download}> Download the collection! </button>
                      <a hidden={true} download={this.fileNames[this.state.fileType]} href={this.state.fileDownloadUrl} ref={e=>this.dofileDownload = e}>Go</a>
                    </div>
                }
                {this.state.resultSearchNasaNEO.element_count !== 0 && <ChartNearObject resultSearchNasaNEO = {this.state.resultSearchNasaNEO}/>}
            </div>
          </div>
          <Footer/>
      </div>
    )
  }
}

export default Hobby;