import React, { Component } from 'react'
import ReactPlayer from "react-player";
import Lightroom from 'react-lightbox-gallery'

export default class App extends Component {
  render() {
    var images = [];
    images = [
      {
        src: "../img/photo-about-me.jpg",
        desc: ":)",
        sub: ":)"
      }
    ]
    if(this.props.resultSearchNasaAPOD.length !== 0){
      images = [];
    }
    this.props.resultSearchNasaAPOD.map(function(item, index) {
      if([item.url].filter(it => (it.includes(".jpg") || it.includes(".jpeg") || it.includes(".bmp") || it.includes(".png") || it.includes(".gif") || it.includes(".tiff"))).length !== 0){
        images.push({
          src: item.url,
          desc: item.explanation,
          sub: item.date + " " + item.title + " by " + (item.copyright || "-")
        });
      }
    });
    var settings = {
      columnCount:{
        default:5,
        mobile:3,
        tab:4
      },
      mode: 'dark'//light
    }
    return (
      <div>
        <Lightroom images={images} settings={settings} />
      </div>
    )
  }
}