import React from "react";
import Footer from './footer';
import Header from './headers'
import "./aboutMe.css";
import 'react-notifications/lib/notifications.css';
import emailjs from 'emailjs-com';

class AboutMe extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
        id: null
    };
  }

  componentDidMount() {
    console.log("About-me");
  }
  sendApply(e) {
    e.preventDefault();
    console.log(process.env.TARGET);
    emailjs.sendForm(process.env.REACT_APP_SERVICE_ID, process.env.REACT_APP_TEMPLATE_ID, e.target, process.env.REACT_APP_TARGET)
      .then((result) => {
        console.log(result.text);
      }, (error) => {
        console.log(error.text);
      });
      e.target.reset();
  }
  render() {
    const { id } = this.state
    return (
      <div>
        <div id="header"><Header/></div>
        <div  id="main-wrapper-about-me">
          <div id="wrapper-about-me" className="wrapper-about-me-class">
            <div id="wrapper-about-me-left" className="wrapper-about-me-commun">
              <div className="about-me-commun">
              <img className="img-fluid img-fluid-about-me" id="img-me" src={`../img/photo-about-me.jpg`}/>
              </div>
            </div>
            <div id="wrapper-about-me-right" className="wrapper-about-me-commun">
              <div id="about-me-right" className="about-me-commun">  
                <h1 className="h1-about-me">Fmi Omar,</h1><br/>
                <div><span>Dorigine bninoise rsidant en France depuis 2015. Ingnieur financier de formation, je suis un grand passionn du web et de tout son cosystme.
                Vous pouvez retrouver des projets (ayant aboutis ou pas ;) dans diffrents langages) dans la rubrique Work ainsi que mon parcours professionnel dans la rubrique Resume.
                <br/> Quand on te demandera ce qui sest pass, dis-leur que le Nord se souvient. Dis-leur que lhiver est venu pour la maison Frey.  Arya Stark - GoT.</span></div>
                <br/>
                <div className="flex-icon">
                  <div className="social-icon icon-box icon-box-top icon-inline"><a href="#" target="_blank"><i className="fa fa-youtube-play"></i></a></div>
                  <div className="social-icon icon-box icon-box-top icon-inline"><a href="#" target="_blank"><i className="fa fa-instagram"></i></a></div>
                  <div className="social-icon icon-box icon-box-top icon-inline"><a href="#" target="_blank"><i className="fa fa-twitter"></i></a></div>
                  <div className="social-icon icon-box icon-box-top icon-inline"><a href="#" target="_blank"><i className="fa fa-twitch"></i></a></div>
                </div>
                <br/>
                <div><p className="p-about-me">Pour tout renseignements supplmentaires,
                veuillez me contacter en utilisant le formulaire ci-dessous.
                </p></div>
                    
                <form onSubmit={this.sendApply} method="POST"  className="forms_contact_about_me" id="id_forms_contact_about_me">
                    <div className="details_contact_about_me col-sm-9">
                    <div className="contact_about_me_input">
                        <div><label>Nom<span className="text-required">*</span></label></div>
                        <input onChange={this.handleChange} type="text" className="input_contact_about_me" name="nom_contact" id="nom_contact" required/>
                    </div><br/>
                    <div className="contact_about_me_input">
                        <div><label>Email<span className="text-required">*</span></label></div>
                        <input onChange={this.handleChange} type="email" className="input_contact_about_me" name="email_contact" id="email_contact" required/>
                    </div><br/>
                    <div className="contact_about_me_input">
                        <div><label>Sujet<span className="text-required">*</span></label></div>
                        <input onChange={this.handleChange} type="text" className="input_contact_about_me" name="numero_contact" id="numero_contact" required/>
                    </div><br/>
                    <div className="contact_about_me_input">
                        <div><label>Message<span className="text-required">*</span></label></div>
                        <textarea onChange={this.handleChange} type="text" className="input_contact_about_me" name="message_contact" id="message_contact" required/>
                    </div>
                    </div>
                    <br/>
                    <div className="politique-confid">
                        <label>
                          <input type="checkbox"/><span className="confirm_span">Confirmez que vous avez lu <a href="#">la Politique de confidentialit</a></span>.
                        </label>
                      </div>
                      <br/>
                    <div className="">
                        <input id="sendApply" className="btn btn-outline-success" type="submit" />
                    </div>
                </form>
                {/* <NotificationContainer/> */}
              </div>
            </div>
          </div>
        </div>
        <Footer/>
      </div>
    )
  }
}

export default AboutMe;